import stringMethods from '../library/stringMethods';

const DefaultClientFields = ({fields} : any) => {

const fieldNames = fields.map((e : any) => {

const fieldName = new stringMethods(e.fieldName).camelStringToText().firstCharUpperCase().text;

return <li key={e._id}>{fieldName}</li>
})
    
    return (
        <div>
            <h2>Default client fields</h2>
            <ul>{fieldNames}</ul>
        </div>
    )
}

export default DefaultClientFields;